import {
  Button,
  Group,
  NumberInput,
  Select,
  Space,
  Stack,
  Text,
  TextInput,
  Title,
} from "@mantine/core";
import "./App.css";
import { useState } from "react";

const W1 = [1, 1, 2];
const W2 = [1, 1, 1, 1];

function App() {
  const [rowWeights1, setRowWeights1] = useState(W1);
  const [rowWeights2, setRowWeights2] = useState(W2);

  const handleUpdateRowWeights1 = (rowWeights: Array<number>): void => {
    setRowWeights1(rowWeights);
  };
  const handleUpdateRowWeights2 = (rowWeights: Array<number>): void => {
    setRowWeights2(rowWeights);
  };

  return (
    <Stack p={32}>
      <Title order={3}>Settings</Title>
      <Settings
        rowWeights1={rowWeights1}
        rowWeights2={rowWeights2}
        onUpdateRowWeights1={handleUpdateRowWeights1}
        onUpdateRowWeights2={handleUpdateRowWeights2}
      />

      <Space />
      <Title order={3}>Layout</Title>
      <Layout rowWeights1={rowWeights1} rowWeights2={rowWeights2} />
    </Stack>
  );
}

/////

type SettingsProps = {
  rowWeights1: Array<number>;
  rowWeights2: Array<number>;
  onUpdateRowWeights1: (rowWeights: Array<number>) => void;
  onUpdateRowWeights2: (rowWeights: Array<number>) => void;
};
function Settings(props: SettingsProps) {
  const rowWeights1 = props.rowWeights1;
  const rowWeights2 = props.rowWeights2;

  const handleChange1 = (event: any) => {
    if (event.key === "Enter") {
      const value = event.currentTarget.value;
      const numbers = tryParse(value, rowWeights1);
      props.onUpdateRowWeights1(numbers);
    }
  };
  const handleChange2 = (event: any) => {
    if (event.key === "Enter") {
      const value = event.currentTarget.value;
      const numbers = tryParse(value, rowWeights2);
      props.onUpdateRowWeights2(numbers);
    }
  };
  const tryParse = (input: string, fallback: Array<number>): Array<number> => {
    try {
      return input.split(",").map((token: string) => parseInt(token.trim()));
    } catch (error: any) {
      return fallback;
    }
  };

  return (
    <Stack>
      <Text size={"sm"} weight={"bold"}>
        Each field has a weight between 1 and 5.
      </Text>
      <Group>
        <TextInput
          defaultValue={rowWeights1.join(", ")}
          onKeyUp={handleChange1}
          label="Row 1 Weights"
        />
        <TextInput
          defaultValue={rowWeights2.join(", ")}
          onKeyUp={handleChange2}
          label="Row 2 Weights"
        />
      </Group>
      <Text size={"sm"} weight={"bold"}>
        Press 'Enter' after each modification in order to update the layout.
      </Text>
    </Stack>
  );
}

/////

type LayoutProps = {
  rowWeights1: Array<number>;
  rowWeights2: Array<number>;
};
function Layout(props: LayoutProps) {
  const sum = (numbers: Array<number>): number => {
    return numbers.reduce((accumulator, currentValue) => {
      return accumulator + currentValue;
    }, 0);
  };

  const rowWeights1 = props.rowWeights1;
  const rowWeights2 = props.rowWeights2;

  const rowTotal1 = sum(rowWeights1);
  const rowTotal2 = sum(rowWeights2);

  return (
    <div className="container">
      <div
        className="row-grid "
        style={{ gridTemplateColumns: "repeat(" + rowTotal1 + ", 1fr)" }}
      >
        <Select
          className={"weight-" + rowWeights1[0]}
          label="Flow"
          data={[
            { value: "Algo", label: "Algo" },
            { value: "DMA", label: "DMA" },
            { value: "Worked", label: "Worked" },
          ]}
          value="Algo"
          withAsterisk
        />
        <Select
          className={"weight-" + rowWeights1[1]}
          label="Type"
          data={[
            { value: "Limit", label: "Limit" },
            { value: "Market", label: "Market" },
            { value: "Stop", label: "Stop" },
          ]}
          value="Limit"
          withAsterisk
        />
        <NumberInput
          className={"weight-" + rowWeights1[2]}
          defaultValue={100}
          label="Quantity"
          withAsterisk
        />
      </div>
      <div
        className="row-grid"
        style={{ gridTemplateColumns: "repeat(" + rowTotal2 + ", 1fr)" }}
      >
        <NumberInput
          className={"weight-" + rowWeights2[0]}
          defaultValue={145}
          label="Limit Price"
        />
        <NumberInput
          className={"weight-" + rowWeights2[1]}
          disabled
          label="Stop Price"
        />
        <NumberInput
          className={"weight-" + rowWeights2[2]}
          disabled
          label="Display Quantity"
        />
        <NumberInput
          className={"weight-" + rowWeights2[3]}
          defaultValue={14500}
          label="Notional"
        />
      </div>
    </div>
  );
}

/////

export default App;
